@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  box-sizing: border-box; }

input:-internal-autofill-previewed,
input:-internal-autofill-selected,
textarea:-internal-autofill-previewed,
textarea:-internal-autofill-selected,
select:-internal-autofill-previewed,
select:-internal-autofill-selected {
  -webkit-text-fill-color: #333;
  -webkit-box-shadow: 0 0 0 50px white inset;
  /* можно изменить на любой вариант цвета */
  outline: none;
  border: none; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after {
  content: '';
  content: none; }

q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

.App {
  position: relative; }
  .App:before {
    position: absolute;
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    z-index: 20000;
    background-color: #160c0d;
    opacity: 0.3; }

.wrapper {
  display: flex;
  justify-content: center; }

body {
  min-width: 320px;
  max-width: 2560px;
  margin: auto;
  position: relative; }

.container {
  min-width: 320px;
  max-width: 1170px;
  width: 100%;
  overflow: hidden; }

h1 {
  font-family: 'Overpass';
  font-weight: 600;
  font-size: 30px;
  color: white; }

h2 {
  font-family: 'Overpass';
  font-weight: 600;
  font-size: 26px; }

h3 {
  font-family: 'Overpass';
  font-weight: 600;
  font-size: 22px; }

h4 {
  font-family: 'Overpass';
  font-weight: 400;
  font-size: 20px; }

h5 {
  font-family: 'Overpass';
  font-weight: 400;
  font-size: 16px; }

.loadphoto-block .loadphoto__text {
  font-family: 'Source Sans Pro';
  font-weight: 400;
  font-size: 12px;
  color: #8d8c8c; }

@media (min-width: 768px) {
  h1 {
    font-size: 42px; }
  h2 {
    font-size: 42px; }
  h3 {
    font-size: 26px; }
  h4 {
    font-size: 22px; }
  h5 {
    font-size: 18px; } }

@media (min-width: 1024px) {
  h1 {
    font-size: 50px; } }

.loadphoto-block {
  width: 100%; }
  .loadphoto-block form {
    display: flex;
    border-radius: 0.25rem;
    font-size: 1rem; }
    .loadphoto-block form .form-input {
      width: 100%;
      font-size: 1rem;
      color: #8d8c8c;
      outline: none;
      font-family: "Source Sans Pro";
      padding: 1rem 1rem;
      border: 1px solid #8d8c8c;
      border-right: none;
      border-top-left-radius: 0.25rem;
      border-bottom-left-radius: 0.25rem; }
    .loadphoto-block form .input-notValid {
      border: 1px solid red; }
    .loadphoto-block form .loadphoto__btn {
      padding: 0.5rem 0.625rem;
      background-color: transparent;
      font-family: "Source Sans Pro";
      font-weight: 700;
      font-size: 1.25rem;
      border: 2px solid #ef6c00;
      border-top-right-radius: 0.25rem;
      border-bottom-right-radius: 0.25rem;
      color: #ef6c00;
      outline: none; }
      .loadphoto-block form .loadphoto__btn:hover {
        border: 2px solid #fa8331;
        color: #fa8331; }
  .loadphoto-block .loadphoto__text {
    margin-top: 0.3125rem;
    padding: 0 1.0625rem; }

@media (min-width: 768px) {
  .loadphoto-block {
    width: 48%;
    align-self: flex-start; }
    .loadphoto-block form .form-input {
      padding: 0.96875rem 1rem; }
    .loadphoto-block form .loadphoto__btn {
      padding: 0.4375rem 2.0625rem; }
    .loadphoto-block .loadphoto__text {
      margin-top: 0.3125rem; } }

@media (min-width: 1024px) {
  .loadphoto-block {
    width: 48.5%; } }
