@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  box-sizing: border-box; }

input:-internal-autofill-previewed,
input:-internal-autofill-selected,
textarea:-internal-autofill-previewed,
textarea:-internal-autofill-selected,
select:-internal-autofill-previewed,
select:-internal-autofill-selected {
  -webkit-text-fill-color: #333;
  -webkit-box-shadow: 0 0 0 50px white inset;
  /* можно изменить на любой вариант цвета */
  outline: none;
  border: none; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after {
  content: '';
  content: none; }

q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

.App {
  position: relative; }
  .App:before {
    position: absolute;
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    z-index: 20000;
    background-color: #160c0d;
    opacity: 0.3; }

.wrapper {
  display: flex;
  justify-content: center; }

body {
  min-width: 320px;
  max-width: 2560px;
  margin: auto;
  position: relative; }

.container {
  min-width: 320px;
  max-width: 1170px;
  width: 100%;
  overflow: hidden; }

h1 {
  font-family: 'Overpass';
  font-weight: 600;
  font-size: 30px;
  color: white; }

h2 {
  font-family: 'Overpass';
  font-weight: 600;
  font-size: 26px; }

h3 {
  font-family: 'Overpass';
  font-weight: 600;
  font-size: 22px; }

h4 {
  font-family: 'Overpass';
  font-weight: 400;
  font-size: 20px; }

h5 {
  font-family: 'Overpass';
  font-weight: 400;
  font-size: 16px; }

.technologies__item .technologies__item-text {
  font-family: 'Source Sans Pro';
  font-weight: 400;
  font-size: 16px;
  color: black; }

@media (min-width: 768px) {
  h1 {
    font-size: 42px; }
  h2 {
    font-size: 42px; }
  h3 {
    font-size: 26px; }
  h4 {
    font-size: 22px; }
  h5 {
    font-size: 18px; } }

@media (min-width: 1024px) {
  h1 {
    font-size: 50px; } }

.technologies-block {
  padding: 4rem 1.25rem 4.375rem 1.25rem; }
  .technologies-block .technologies-block__title {
    line-height: 1.875rem;
    margin-bottom: 2.0625rem;
    text-align: center; }
  .technologies-block .technologies-block__list {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center; }

.technologies__item {
  margin-bottom: 1.875rem;
  padding: 0 1px;
  text-align: center; }
  .technologies__item:last-child {
    margin-bottom: 0; }
  .technologies__item .technologies__item-img {
    display: block;
    width: 32%;
    margin: auto;
    margin-bottom: 0.6875rem; }
  .technologies__item .technologies__item-subtitle {
    margin-bottom: 0.375rem; }
  .technologies__item .technologies__item-text {
    line-height: 1.375rem;
    text-align: center;
    color: #4c4b4b; }

@media (min-width: 768px) {
  .technologies-block {
    padding: 7.9375rem 5.25rem 7.875rem 5.375rem; }
    .technologies-block .technologies-block__title {
      line-height: 3.1875rem;
      margin-bottom: 2.75rem; }
    .technologies-block .technologies__item {
      display: flex;
      justify-content: space-between;
      margin-bottom: 3rem; }
      .technologies-block .technologies__item:last-child {
        margin-bottom: 0; }
      .technologies-block .technologies__item .technologies__item-img {
        align-self: flex-start;
        width: 15%;
        margin: 0; }
      .technologies-block .technologies__item div {
        width: 79.5%; }
        .technologies-block .technologies__item div .technologies__item-subtitle {
          text-align: left;
          margin-bottom: 0.625rem; }
        .technologies-block .technologies__item div .technologies__item-text {
          text-align: left;
          line-height: 1.375rem; } }

@media (min-width: 1024px) {
  .technologies-block {
    padding: 7.0625rem 2.8125rem 8.75rem 2.8125rem; }
    .technologies-block .technologies-block__title {
      width: 60%;
      margin: auto;
      margin-bottom: 2.9375rem;
      padding-left: 0; }
    .technologies-block .technologies-block__list {
      flex-direction: row;
      justify-content: space-between;
      align-items: flex-start; }
      .technologies-block .technologies-block__list .technologies__item {
        width: 31.2%;
        flex-direction: column;
        margin-bottom: 0; }
        .technologies-block .technologies-block__list .technologies__item .technologies__item-img {
          width: 34%;
          margin: 0;
          margin-bottom: 1.49375rem; }
        .technologies-block .technologies-block__list .technologies__item div {
          width: 100%; }
          .technologies-block .technologies-block__list .technologies__item div .technologies__item-subtitle {
            margin-bottom: 0.875rem; } }

@media (min-width: 2400px) {
  .technologies-block {
    padding: 3.9375rem 0.875rem 8.75rem 1.25rem; }
    .technologies-block .technologies-block__title {
      width: 54%; }
    .technologies-block .technologies-block__list .technologies__item {
      width: 31.2%; }
      .technologies-block .technologies-block__list .technologies__item .technologies__item-img {
        width: 28%;
        margin-bottom: 1.625rem; } }
