@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  box-sizing: border-box; }

input:-internal-autofill-previewed,
input:-internal-autofill-selected,
textarea:-internal-autofill-previewed,
textarea:-internal-autofill-selected,
select:-internal-autofill-previewed,
select:-internal-autofill-selected {
  -webkit-text-fill-color: #333;
  -webkit-box-shadow: 0 0 0 50px white inset;
  /* можно изменить на любой вариант цвета */
  outline: none;
  border: none; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after {
  content: '';
  content: none; }

q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

.App {
  position: relative; }
  .App:before {
    position: absolute;
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    z-index: 20000;
    background-color: #160c0d;
    opacity: 0.3; }

.wrapper {
  display: flex;
  justify-content: center; }

body {
  min-width: 320px;
  max-width: 2560px;
  margin: auto;
  position: relative; }

.container {
  min-width: 320px;
  max-width: 1170px;
  width: 100%;
  overflow: hidden; }

h1 {
  font-family: 'Overpass';
  font-weight: 600;
  font-size: 30px;
  color: white; }

h2 {
  font-family: 'Overpass';
  font-weight: 600;
  font-size: 26px; }

h3 {
  font-family: 'Overpass';
  font-weight: 600;
  font-size: 22px; }

h4 {
  font-family: 'Overpass';
  font-weight: 400;
  font-size: 20px; }

h5 {
  font-family: 'Overpass';
  font-weight: 400;
  font-size: 16px; }

.users-block .users-block__text {
  font-family: "PT Sans";
  font-weight: 400;
  font-size: 16px;
  text-align: center;
  line-height: 20px; }

.users-block .users-block__btn {
  border-radius: 4px;
  cursor: pointer;
  border: none;
  font-family: "Source Sans Pro";
  font-weight: 700;
  font-size: 20px;
  outline: none; }

@media (min-width: 768px) {
  h1 {
    font-size: 42px; }
  h2 {
    font-size: 42px; }
  h3 {
    font-size: 26px; }
  h4 {
    font-size: 22px; }
  h5 {
    font-size: 18px; }
  .users-block .users-block__btn {
    border-radius: 4px;
    padding: 10px 0; }
  .users-block .users-block__text {
    font-size: 18px;
    text-align: center;
    line-height: 20px; } }

@media (min-width: 1024px) {
  h1 {
    font-size: 50px; }
  .users-block .users-block__text {
    font-size: 18px;
    text-align: center;
    line-height: 20px; } }

.wrapper-spinner {
  position: relative; }

.users-block {
  padding: 4rem 1rem 0px 1.125rem; }
  .users-block .users-block__title {
    margin-bottom: 1rem;
    line-height: 1.9375rem;
    text-align: center; }
  .users-block .users-block__text {
    padding: 0 3.75rem;
    line-height: 1.375rem;
    margin-bottom: 1.875rem; }
  .users-block .users-block__btn {
    width: 100%;
    display: block;
    margin: auto;
    margin-top: 1.875rem;
    padding: 0.375rem 0 0.375rem 0;
    border: 2px solid #ef6c00;
    color: #ef6c00;
    background-color: transparent;
    transition: 1s; }
    .users-block .users-block__btn:hover {
      background-color: #FDE6D3; }
  .users-block .users-block__btn--disabled {
    color: #8d8c8c;
    background-color: #d7d7d7;
    border: 2px solid  #d7d7d7; }
  .users-block .users-list {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center; }

@media (min-width: 768px) {
  .users-block {
    padding: 6.875rem 1.625rem 0px 1.625rem; }
    .users-block .users-block__title {
      line-height: 3.1875rem; }
    .users-block .users-block__text {
      line-height: 1.5rem;
      margin-bottom: 2.3125rem; }
    .users-block .users-list {
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between; }
    .users-block .users-block__btn {
      width: 40.5%;
      margin-top: 3.375rem;
      padding: 0.375rem 0; } }

@media (min-width: 1024px) {
  .users-block {
    padding: 8rem 2.9375rem 0px 2.9375rem; }
    .users-block .users-block__title {
      margin-bottom: 0.6875rem;
      line-height: 3.75rem; }
    .users-block .users-block__text {
      margin-bottom: 3.4375rem;
      padding: 0; }
    .users-block .users-block__btn {
      width: 31%; } }

@media (min-width: 2400px) {
  .users-block {
    padding: 8.125rem 0.625rem 0px 1.4375rem; }
    .users-block .users-block__text {
      margin-bottom: 3.625rem; }
    .users-block .users-block__btn {
      width: 26%; } }
