@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  box-sizing: border-box; }

input:-internal-autofill-previewed,
input:-internal-autofill-selected,
textarea:-internal-autofill-previewed,
textarea:-internal-autofill-selected,
select:-internal-autofill-previewed,
select:-internal-autofill-selected {
  -webkit-text-fill-color: #333;
  -webkit-box-shadow: 0 0 0 50px white inset;
  /* можно изменить на любой вариант цвета */
  outline: none;
  border: none; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after {
  content: '';
  content: none; }

q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

.App {
  position: relative; }
  .App:before {
    position: absolute;
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    z-index: 20000;
    background-color: #160c0d;
    opacity: 0.3; }

.wrapper {
  display: flex;
  justify-content: center; }

body {
  min-width: 320px;
  max-width: 2560px;
  margin: auto;
  position: relative; }

.container {
  min-width: 320px;
  max-width: 1170px;
  width: 100%;
  overflow: hidden; }

h1 {
  font-family: 'Overpass';
  font-weight: 600;
  font-size: 30px;
  color: white; }

h2 {
  font-family: 'Overpass';
  font-weight: 600;
  font-size: 26px; }

h3 {
  font-family: 'Overpass';
  font-weight: 600;
  font-size: 22px; }

h4 {
  font-family: 'Overpass';
  font-weight: 400;
  font-size: 20px; }

h5 {
  font-family: 'Overpass';
  font-weight: 400;
  font-size: 16px; }

.user-block .user-position,
.user-block .user-email,
.user-block .user-phone {
  font-family: 'Source Sans Pro';
  font-weight: 400;
  font-size: 14px; }

.user-block .user-name:hover::before, .user-block .user-email:hover::before {
  content: attr(dataTitle);
  display: block;
  z-index: 999;
  position: absolute;
  border-radius: 4px;
  font-family: "Source Sans Pro";
  font-size: 10px;
  padding: 5px 5px;
  color: white;
  background-color: #707070; }

@media (min-width: 768px) {
  h1 {
    font-size: 42px; }
  h2 {
    font-size: 42px; }
  h3 {
    font-size: 26px; }
  h4 {
    font-size: 22px; }
  h5 {
    font-size: 18px; } }

@media (min-width: 1024px) {
  h1 {
    font-size: 50px; } }

.user-block {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: stretch;
  width: 100%;
  text-align: center;
  margin-bottom: 1.8125rem; }
  .user-block:last-child {
    margin-bottom: 0; }
  .user-block .user-block-photo {
    padding: 0;
    margin: auto;
    border-radius: 50%;
    display: block;
    width: 4.375rem;
    height: 4.375rem; }
  .user-block .user-name {
    display: block;
    position: relative;
    padding: 0.5625rem 0;
    font-size: 1.375rem;
    line-height: 1.5625rem;
    font-weight: 600; }
    .user-block .user-name:hover::before {
      top: -10%;
      left: 20%; }
  .user-block .user-position,
  .user-block .user-email,
  .user-block .user-phone {
    padding-bottom: 0.3125rem; }
  .user-block .user-email {
    position: relative; }
    .user-block .user-email:hover::before {
      top: 0;
      left: 50%; }

@media (min-width: 768px) {
  .user-block {
    width: 30%;
    margin-bottom: 2.1875rem;
    align-self: flex-start; }
    .user-block:nth-last-child(-n+3) {
      margin-bottom: 0px; }
    .user-block .user-name {
      padding: 0.6875rem 0 0.5rem 0; } }

@media (min-width: 1024px) {
  .user-block {
    width: 31%;
    flex-direction: row;
    margin-bottom: 4.75rem; }
    .user-block:nth-last-child(-n+3) {
      margin-bottom: 0px; }
    .user-block .user-block-photo {
      margin: 0;
      margin-right: 0.625rem; }
    .user-block .user-block__info {
      padding-top: 0.9375rem; }
      .user-block .user-block__info .user-position,
      .user-block .user-block__info .user-email,
      .user-block .user-block__info .user-phone,
      .user-block .user-block__info .user-name {
        text-align: left; }
      .user-block .user-block__info .user-name {
        padding: 0;
        padding-top: 0.4375rem;
        margin-bottom: 0.75rem; } }
