@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  box-sizing: border-box; }

input:-internal-autofill-previewed,
input:-internal-autofill-selected,
textarea:-internal-autofill-previewed,
textarea:-internal-autofill-selected,
select:-internal-autofill-previewed,
select:-internal-autofill-selected {
  -webkit-text-fill-color: #333;
  -webkit-box-shadow: 0 0 0 50px white inset;
  /* можно изменить на любой вариант цвета */
  outline: none;
  border: none; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after {
  content: '';
  content: none; }

q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

.App {
  position: relative; }
  .App:before {
    position: absolute;
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    z-index: 20000;
    background-color: #160c0d;
    opacity: 0.3; }

.wrapper {
  display: flex;
  justify-content: center; }

body {
  min-width: 320px;
  max-width: 2560px;
  margin: auto;
  position: relative; }

.container {
  min-width: 320px;
  max-width: 1170px;
  width: 100%;
  overflow: hidden; }

h1 {
  font-family: 'Overpass';
  font-weight: 600;
  font-size: 30px;
  color: white; }

h2 {
  font-family: 'Overpass';
  font-weight: 600;
  font-size: 26px; }

h3 {
  font-family: 'Overpass';
  font-weight: 600;
  font-size: 22px; }

h4 {
  font-family: 'Overpass';
  font-weight: 400;
  font-size: 20px; }

h5 {
  font-family: 'Overpass';
  font-weight: 400;
  font-size: 16px; }

.footer-contacts .footer-contacts__item a {
  font-family: 'Source Sans Pro';
  font-weight: 400;
  font-size: 20px; }

@media (min-width: 768px) {
  h1 {
    font-size: 42px; }
  h2 {
    font-size: 42px; }
  h3 {
    font-size: 26px; }
  h4 {
    font-size: 22px; }
  h5 {
    font-size: 18px; } }

@media (min-width: 1024px) {
  h1 {
    font-size: 50px; } }

.footer-content {
  width: 70%;
  margin: auto;
  padding-top: 1.625rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center; }

.footer-contacts {
  padding: 0; }
  .footer-contacts .footer-contacts__item {
    margin-bottom: 1.625rem;
    text-align: center; }
    .footer-contacts .footer-contacts__item:last-child {
      margin-bottom: 0; }
    .footer-contacts .footer-contacts__item a {
      align-self: center;
      color: white;
      text-decoration: none; }
    .footer-contacts .footer-contacts__item i {
      display: none; }

.footer-content__nav {
  display: none; }

@media (min-width: 768px) {
  .footer-content {
    padding: 1.4375rem 1.5625rem 0px 1.4375rem;
    width: 100%;
    flex-direction: row;
    justify-content: space-between; }
  .footer-contacts {
    width: 25%; }
    .footer-contacts .footer-contacts__item {
      text-align: left;
      margin-bottom: 1.25rem; }
      .footer-contacts .footer-contacts__item:last-child {
        margin-bottom: 0; }
      .footer-contacts .footer-contacts__item a {
        font-size: 1.125rem;
        line-height: 1.5rem; }
  .footer-content__nav {
    width: 70%;
    display: flex;
    flex-wrap: wrap; }
    .footer-content__nav .footer-content__item {
      width: 25%;
      text-align: right; }
      .footer-content__nav .footer-content__item a {
        font-family: 'Source Sans Pro';
        font-weight: 400;
        font-size: 1rem;
        line-height: 1.875rem;
        color: white;
        text-decoration: none; }
        .footer-content__nav .footer-content__item a:hover {
          color: #ef6c00; } }

@media (min-width: 1024px) {
  .footer-content {
    padding: 1.25rem 2.75rem 0px 2.9375rem; }
    .footer-content .footer-contacts__item {
      display: flex;
      align-items: center;
      margin-bottom: 0.8125rem; }
      .footer-content .footer-contacts__item:hover svg {
        fill: #ef6c00; }
      .footer-content .footer-contacts__item:hover a {
        color: #ef6c00; }
      .footer-content .footer-contacts__item i {
        display: flex;
        align-items: center;
        margin-right: 0.625rem;
        width: 1.875rem;
        height: 1.875rem; }
        .footer-content .footer-contacts__item i svg {
          width: 1.5625rem;
          height: 1.5625rem;
          fill: white; }
      .footer-content .footer-contacts__item a {
        align-self: center;
        color: white;
        text-decoration: none;
        font-size: 20px; } }

@media (min-width: 2400px) {
  .footer-content {
    padding: 1.25rem 0.625rem 0px 1rem; }
    .footer-content .footer-contacts__item i {
      margin-right: 0.3125rem; }
    .footer-content .footer-content__nav {
      width: 68%; } }
