@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  box-sizing: border-box; }

input:-internal-autofill-previewed,
input:-internal-autofill-selected,
textarea:-internal-autofill-previewed,
textarea:-internal-autofill-selected,
select:-internal-autofill-previewed,
select:-internal-autofill-selected {
  -webkit-text-fill-color: #333;
  -webkit-box-shadow: 0 0 0 50px white inset;
  /* можно изменить на любой вариант цвета */
  outline: none;
  border: none; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after {
  content: '';
  content: none; }

q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

.App {
  position: relative; }
  .App:before {
    position: absolute;
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    z-index: 20000;
    background-color: #160c0d;
    opacity: 0.3; }

.wrapper {
  display: flex;
  justify-content: center; }

body {
  min-width: 320px;
  max-width: 2560px;
  margin: auto;
  position: relative; }

.container {
  min-width: 320px;
  max-width: 1170px;
  width: 100%;
  overflow: hidden; }

h1 {
  font-family: 'Overpass';
  font-weight: 600;
  font-size: 30px;
  color: white; }

h2 {
  font-family: 'Overpass';
  font-weight: 600;
  font-size: 26px; }

h3 {
  font-family: 'Overpass';
  font-weight: 600;
  font-size: 22px; }

h4 {
  font-family: 'Overpass';
  font-weight: 400;
  font-size: 20px; }

h5 {
  font-family: 'Overpass';
  font-weight: 400;
  font-size: 16px; }

@media (min-width: 768px) {
  h1 {
    font-size: 42px; }
  h2 {
    font-size: 42px; }
  h3 {
    font-size: 26px; }
  h4 {
    font-size: 22px; }
  h5 {
    font-size: 18px; } }

@media (min-width: 1024px) {
  h1 {
    font-size: 50px; } }

.footer-socials {
  padding: 1.875rem 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: stretch; }
  .footer-socials p {
    color: white;
    align-self: center;
    font-weight: 400;
    font-size: 0.75rem;
    font-family: "Source Sans Pro"; }

.socials-bar {
  width: 100%;
  text-align: center;
  padding: 0 0.625rem;
  margin-bottom: 0.9375rem; }
  .socials-bar .socials-bar__link {
    margin-right: 1.25rem; }
    .socials-bar .socials-bar__link:last-child {
      margin-right: 0; }
    .socials-bar .socials-bar__link svg {
      width: 1.625rem;
      height: 1.625rem;
      fill: white; }
      .socials-bar .socials-bar__link svg:hover {
        fill: #ef6c00; }

@media (min-width: 768px) {
  .footer-socials {
    padding: 1.875rem 1.5625rem 1.875rem 1.375rem;
    flex-direction: row;
    justify-content: space-between; }
    .footer-socials p {
      width: 50%;
      order: -1;
      padding-top: 0.3125rem;
      align-self: flex-start; }
  .socials-bar {
    width: 50%;
    display: flex;
    justify-content: flex-end;
    margin-bottom: 0px;
    padding: 0; }
    .socials-bar .socials-bar__link {
      margin-right: 1.25rem; } }

@media (min-width: 1024px) {
  .footer-socials {
    padding: 2rem 2.9375rem 2.375rem 2.9375rem; }
    .footer-socials p {
      padding-top: 0.875rem; } }

@media (min-width: 2400px) {
  .footer-socials {
    padding: 1.8125rem 1.0625rem 2.375rem 1.3125rem; } }
