@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  box-sizing: border-box; }

input:-internal-autofill-previewed,
input:-internal-autofill-selected,
textarea:-internal-autofill-previewed,
textarea:-internal-autofill-selected,
select:-internal-autofill-previewed,
select:-internal-autofill-selected {
  -webkit-text-fill-color: #333;
  -webkit-box-shadow: 0 0 0 50px white inset;
  /* можно изменить на любой вариант цвета */
  outline: none;
  border: none; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after {
  content: '';
  content: none; }

q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

.App {
  position: relative; }
  .App:before {
    position: absolute;
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    z-index: 20000;
    background-color: #160c0d;
    opacity: 0.3; }

.wrapper {
  display: flex;
  justify-content: center; }

body {
  min-width: 320px;
  max-width: 2560px;
  margin: auto;
  position: relative; }

.container {
  min-width: 320px;
  max-width: 1170px;
  width: 100%;
  overflow: hidden; }

h1 {
  font-family: 'Overpass';
  font-weight: 600;
  font-size: 30px;
  color: white; }

h2 {
  font-family: 'Overpass';
  font-weight: 600;
  font-size: 26px; }

h3 {
  font-family: 'Overpass';
  font-weight: 600;
  font-size: 22px; }

h4 {
  font-family: 'Overpass';
  font-weight: 400;
  font-size: 20px; }

h5 {
  font-family: 'Overpass';
  font-weight: 400;
  font-size: 16px; }

.registration-form .registration-form__btn:disabled,
.registration-form .registration-form__btn {
  border-radius: 4px;
  cursor: pointer;
  border: none;
  font-family: "Source Sans Pro";
  font-weight: 700;
  font-size: 20px;
  outline: none; }

@media (min-width: 768px) {
  h1 {
    font-size: 42px; }
  h2 {
    font-size: 42px; }
  h3 {
    font-size: 26px; }
  h4 {
    font-size: 22px; }
  h5 {
    font-size: 18px; }
  .registration-form .registration-form__btn:disabled,
  .registration-form .registration-form__btn {
    border-radius: 4px;
    padding: 10px 0; } }

@media (min-width: 1024px) {
  h1 {
    font-size: 50px; } }

.registration-form {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: stretch;
  flex-wrap: wrap; }
  .registration-form .registration-form__item {
    position: relative;
    display: block;
    width: 100%;
    margin-bottom: 2.1875rem;
    padding: 1.0625rem 1rem;
    border: 1px solid #8d8c8c;
    border-radius: 0.25rem;
    outline: none;
    transition: .5s; }
    .registration-form .registration-form__item .registration-form-item__header {
      display: inline-block;
      position: absolute;
      top: -14%;
      left: 2%;
      background-color: white;
      padding: 0 0.375rem;
      font-family: "Source Sans Pro";
      font-size: 0.75rem;
      color: #8d8c8c;
      transition: .5s; }
    .registration-form .registration-form__item input {
      width: 100%;
      height: auto;
      font-size: 1rem;
      outline: none;
      color: #8d8c8c;
      font-family: "Source Sans Pro";
      border: none; }
  .registration-form .registration-form__item--invalid {
    color: red;
    font-size: 1rem;
    border: 1px solid red;
    outline: none; }
    .registration-form .registration-form__item--invalid input {
      color: #8d8c8c; }
    .registration-form .registration-form__item--invalid .registration-form-item__header {
      color: red; }
  .registration-form .registration-form__item--valid input {
    color: black; }
  .registration-form .registration-form__btn:disabled,
  .registration-form .registration-form__btn {
    width: 100%;
    margin: auto;
    padding: 0.4375rem 0 0.4375rem 0;
    margin-top: 2.125rem;
    transition: 1s;
    outline: none; }
  .registration-form .registration-form__btn {
    background-color: #ef6c00;
    color: white; }
    .registration-form .registration-form__btn:hover {
      background-color: #FDE6D3; }
  .registration-form .registration-form__btn:disabled {
    cursor: auto;
    color: #8d8c8c;
    background-color: #d7d7d7; }

@media (min-width: 768px) {
  .registration-form {
    flex-direction: row; }
    .registration-form .registration-form__item {
      width: 30.5%;
      margin-bottom: 2.5rem; }
    .registration-form .registration-form__btn:disabled,
    .registration-form .registration-form__btn {
      margin-top: 2.625rem;
      width: 40.5%;
      padding: 0.4375rem 0; } }

@media (min-width: 1024px) {
  .registration-form .registration-form__item {
    width: 31%; }
  .registration-form .registration-form__btn,
  .registration-form .registration-form__btn:disabled {
    width: 31%; } }

@media (min-width: 2400px) {
  .registration-form .registration-form__item {
    width: 31.6%; }
  .registration-form .registration-form__btn,
  .registration-form .registration-form__btn:disabled {
    margin-top: 2.875rem;
    width: 26%; } }
