@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  box-sizing: border-box; }

input:-internal-autofill-previewed,
input:-internal-autofill-selected,
textarea:-internal-autofill-previewed,
textarea:-internal-autofill-selected,
select:-internal-autofill-previewed,
select:-internal-autofill-selected {
  -webkit-text-fill-color: #333;
  -webkit-box-shadow: 0 0 0 50px white inset;
  /* можно изменить на любой вариант цвета */
  outline: none;
  border: none; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after {
  content: '';
  content: none; }

q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

.App {
  position: relative; }
  .App:before {
    position: absolute;
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    z-index: 20000;
    background-color: #160c0d;
    opacity: 0.3; }

.wrapper {
  display: flex;
  justify-content: center; }

body {
  min-width: 320px;
  max-width: 2560px;
  margin: auto;
  position: relative; }

.container {
  min-width: 320px;
  max-width: 1170px;
  width: 100%;
  overflow: hidden; }

h1 {
  font-family: 'Overpass';
  font-weight: 600;
  font-size: 30px;
  color: white; }

h2 {
  font-family: 'Overpass';
  font-weight: 600;
  font-size: 26px; }

h3 {
  font-family: 'Overpass';
  font-weight: 600;
  font-size: 22px; }

h4 {
  font-family: 'Overpass';
  font-weight: 400;
  font-size: 20px; }

h5 {
  font-family: 'Overpass';
  font-weight: 400;
  font-size: 16px; }

.acquinted__about .acquinted__text {
  font-family: 'Source Sans Pro';
  font-weight: 400;
  font-size: 16px;
  color: black; }

@media (min-width: 768px) {
  h1 {
    font-size: 42px; }
  h2 {
    font-size: 42px; }
  h3 {
    font-size: 26px; }
  h4 {
    font-size: 22px; }
  h5 {
    font-size: 18px; } }

@media (min-width: 1024px) {
  h1 {
    font-size: 50px; } }

.acquinted-block {
  padding: 8.1875rem 1rem 0px 1.1875rem; }
  .acquinted-block .acquinted__title {
    text-align: center;
    color: black;
    margin-bottom: 2rem; }

.acquinted__context {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center; }
  .acquinted__context .acquinted__image-block {
    margin-bottom: 1.5625rem; }
    .acquinted__context .acquinted__image-block img {
      display: block;
      height: 13.5rem; }

.acquinted__about .acquinted__subtitle {
  text-align: center;
  line-height: 1.625rem; }

.acquinted__about .acquinted__text {
  line-height: 1.375rem;
  color: #4c4b4b;
  margin: 0.9375rem 0 0.8125rem 0;
  text-align: center; }
  .acquinted__about .acquinted__text:first-of-type {
    margin-bottom: 1.3125rem; }

.acquinted__about .acquinted__btn {
  display: block;
  padding: 4px 0;
  text-align: center;
  color: #ef6c00;
  font-family: "Source Sans Pro";
  font-size: 1.375rem;
  font-weight: 700;
  text-decoration: none;
  background-color: transparent; }
  .acquinted__about .acquinted__btn:hover {
    color: #fa8331; }

@media (min-width: 768px) {
  .acquinted-block {
    padding: 10.9375rem 1.5rem 0px 1.5rem; }
    .acquinted-block .acquinted__title {
      margin-bottom: 2.8125rem;
      line-height: 3.1875rem; }
    .acquinted-block .acquinted__context {
      flex-direction: row;
      justify-content: space-between; }
    .acquinted-block .acquinted__image-block {
      width: 33%;
      align-self: flex-start;
      margin-bottom: 0px; }
    .acquinted-block .acquinted__about {
      width: 65.5%; }
      .acquinted-block .acquinted__about .acquinted__subtitle {
        text-align: left; }
      .acquinted-block .acquinted__about .acquinted__text {
        text-align: left;
        line-height: 1.375rem;
        margin: 1rem 0 1.3125rem 0; }
    .acquinted-block .acquinted__btn {
      text-align: left; } }

@media (min-width: 1024px) {
  .acquinted-block {
    padding: 12rem 2.625rem 0px 2.8125rem; }
    .acquinted-block .acquinted__title {
      margin-bottom: 1.1875rem; }
    .acquinted-block .acquinted__image-block {
      width: 31%;
      align-self: flex-start;
      margin-bottom: 0px;
      padding: 0.4375rem 0; }
      .acquinted-block .acquinted__image-block img {
        width: 100%;
        height: auto; }
    .acquinted-block .acquinted__about {
      width: 65.7%; }
      .acquinted-block .acquinted__about .acquinted__text {
        margin: 0.75rem 0 1.3125rem 0; } }

@media (min-width: 2400px) {
  .acquinted-block {
    padding: 12rem 3.3125rem 0px 3.6875rem; }
    .acquinted-block .acquinted__title {
      margin-bottom: 3rem; }
    .acquinted-block .acquinted__image-block {
      width: 27%; }
    .acquinted-block .acquinted__about {
      align-self: flex-start;
      width: 66.7%; }
      .acquinted-block .acquinted__about .acquinted__text:nth-of-type(2) {
        margin: 0.75rem 0 0.625rem 0; } }
