@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  box-sizing: border-box; }

input:-internal-autofill-previewed,
input:-internal-autofill-selected,
textarea:-internal-autofill-previewed,
textarea:-internal-autofill-selected,
select:-internal-autofill-previewed,
select:-internal-autofill-selected {
  -webkit-text-fill-color: #333;
  -webkit-box-shadow: 0 0 0 50px white inset;
  /* можно изменить на любой вариант цвета */
  outline: none;
  border: none; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after {
  content: '';
  content: none; }

q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

.App {
  position: relative; }
  .App:before {
    position: absolute;
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    z-index: 20000;
    background-color: #160c0d;
    opacity: 0.3; }

.wrapper {
  display: flex;
  justify-content: center; }

body {
  min-width: 320px;
  max-width: 2560px;
  margin: auto;
  position: relative; }

.container {
  min-width: 320px;
  max-width: 1170px;
  width: 100%;
  overflow: hidden; }

h1 {
  font-family: 'Overpass';
  font-weight: 600;
  font-size: 30px;
  color: white; }

h2 {
  font-family: 'Overpass';
  font-weight: 600;
  font-size: 26px; }

h3 {
  font-family: 'Overpass';
  font-weight: 600;
  font-size: 22px; }

h4 {
  font-family: 'Overpass';
  font-weight: 400;
  font-size: 20px; }

h5 {
  font-family: 'Overpass';
  font-weight: 400;
  font-size: 16px; }

.requirements-block .requirements__left-block .requirements__text {
  font-family: 'Source Sans Pro';
  font-weight: 400;
  font-size: 16px;
  color: black; }

.wrapper--requirements {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center; }

@media (min-width: 768px) {
  h1 {
    font-size: 42px; }
  h2 {
    font-size: 42px; }
  h3 {
    font-size: 26px; }
  h4 {
    font-size: 22px; }
  h5 {
    font-size: 18px; } }

@media (min-width: 1024px) {
  h1 {
    font-size: 50px; } }

.wrapper--requirements {
  background-image: url(../../images/bg-2.jpg); }

.requirements-block {
  padding: 4rem 1.1875rem 4.125rem 1.0625rem; }
  .requirements-block .requirements__title {
    margin-bottom: 1.8125rem;
    color: white;
    text-align: center;
    line-height: 1.9375rem; }
  .requirements-block .requirements__content {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch; }
  .requirements-block .requirements__left-block .requirements__text {
    margin-bottom: 1.375rem;
    text-align: center;
    line-height: 1.375rem;
    color: white; }
    .requirements-block .requirements__left-block .requirements__text:last-of-type {
      margin-bottom: 0; }
  .requirements-block .requirements__right-block {
    margin: auto;
    margin-bottom: 1.5rem; }
    .requirements-block .requirements__right-block img {
      display: block;
      width: 16.0625rem;
      height: auto; }

@media (min-width: 768px) {
  .requirements-block {
    padding: 6.875rem 1.875rem 7.625rem 1.4375rem; }
    .requirements-block .requirements__title {
      width: 65.5%;
      line-height: 3.1875rem;
      margin: auto;
      margin-bottom: 2.4375rem; }
    .requirements-block .requirements__content {
      flex-direction: row;
      justify-content: space-between; }
    .requirements-block .requirements__left-block {
      order: -1;
      width: 47%; }
      .requirements-block .requirements__left-block .requirements__text {
        text-align: left;
        margin-bottom: 1.375rem; }
        .requirements-block .requirements__left-block .requirements__text:last-child {
          margin-bottom: 0; }
    .requirements-block .requirements__right-block {
      margin: 0 0;
      width: 48%;
      padding-top: 0.25rem; }
      .requirements-block .requirements__right-block img {
        margin: 0 0;
        display: block;
        height: 25.4375rem;
        width: 100%; } }

@media (min-width: 1024px) {
  .requirements-block {
    padding: 7.8125rem 2.9375rem 9.5rem 2.875rem; }
    .requirements-block .requirements__title {
      width: 100%;
      line-height: 3.75rem;
      margin-bottom: 2.3125rem; }
    .requirements-block .requirements__left-block {
      order: -1;
      align-self: flex-start;
      width: 49%; }
      .requirements-block .requirements__left-block .requirements__text {
        text-align: left;
        margin-bottom: 1.375rem; }
        .requirements-block .requirements__left-block .requirements__text:last-child {
          margin-bottom: 0; }
    .requirements-block .requirements__right-block {
      padding-top: 1.25rem;
      margin: 0 0;
      width: 46%; }
      .requirements-block .requirements__right-block img {
        margin: 0 0;
        display: block;
        height: auto;
        width: 100%; } }

@media (min-width: 2400px) {
  .requirements-block {
    padding: 7.8125rem 0.25rem 6.25rem 1.3125rem; }
    .requirements-block .requirements__title {
      margin-bottom: 2.4375rem; }
    .requirements-block .requirements__left-block {
      width: 48%; }
    .requirements-block .requirements__right-block {
      padding: 0;
      margin-right: 3.75rem;
      width: 38%; } }
